import React, { useEffect, useRef, useState } from 'react';
import ace from 'ace-builds/src-noconflict/ace';
import 'ace-builds/src-noconflict/theme-monokai';
import 'ace-builds/src-noconflict/mode-java';
import 'ace-builds/src-noconflict/mode-plain_text';
import './app.css';

const App = () => {
  const editorRef = useRef(null);
  const outputEditorRef = useRef(null);
  const [isRunning, setIsRunning] = useState(false);

  useEffect(() => {
    const editor = ace.edit(editorRef.current);
    editor.setTheme('ace/theme/monokai');
    editor.getSession().setMode('ace/mode/java');
    editor.setValue('Enter your Java code here');
    editor.setOptions({
      maxLines: 30,
      minLines: 5,
    });

    const outputEditor = ace.edit(outputEditorRef.current);
    outputEditor.setTheme('ace/theme/monokai');
    outputEditor.getSession().setMode('ace/mode/plain_text');
    outputEditor.setReadOnly(true);
    outputEditor.setOptions({
      maxLines: 30,
      minLines: 5,
      showLineNumbers: false,
    });

    window.editor = editor;
    window.outputEditor = outputEditor;
  }, []);

  const runCode = () => {
    const code = window.editor.getValue();
    window.outputEditor.setValue('Running...');
  
    fetch('http://localhost:3000/run-code', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ code }),
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.stdout) {
          window.outputEditor.setValue(data.stdout);
        } else if (data.stderr) {
          window.outputEditor.setValue("[Error] " + data.stderr);
        } else if (data.error) {
          window.outputEditor.setValue("[Error] " + data.error);
        } else {
          window.outputEditor.setValue("[Error] Unknown error occurred.");
        }
      })
      .catch((error) => {
        window.outputEditor.setValue("[Error] " + error.message);
      });

    setIsRunning(true);
  };

  return (
    <div className="bg-gray-100 h-screen">
      <header className="p-6 bg-blue-500 text-center text-white text-2xl rounded-md mb-4">
        <div className="flex justify-between items-center">
          <div className="mx-auto">Java Playground</div>
          {!isRunning && (
            <button
              className="py-2 px-4 bg-green-500 text-white rounded-md run-button ml-auto"
              onClick={runCode}
            >
              Run
            </button>
          )}
        </div>
      </header>
      <main className="flex flex-wrap justify-center items-start h-full overflow-y-auto">
        <div className={`${isRunning ? 'w-1/2' : 'w-full'}`}>
          <div
            ref={editorRef}
            className="w-full h-full p-2 overflow-hidden rounded-md bg-gray-900 text-white"
          ></div>
        </div>
        {isRunning && (
          <div className="w-1/2">
            <div
              ref={outputEditorRef}
              className="w-full h-full p-2 overflow-hidden rounded-md bg-gray-900 text-white"
            ></div>
          </div>
        )}
      </main>
    </div>
  );
};

export default App;
